.nav-link {
  color: var(--main-color);
  font-family: Inter;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 1.2;
}

.nav-link .current {
  color: var(--active-color);
}

@media screen and (min-width: 375px) {
  .container {
    width: 365px;
  }

  #categories {
    width: 326px;
    height: 198px;
    scrollbar-width: thin;
    background: none;
    border: none;
    border-radius: 10px;
    flex-direction: column;
    gap: 18px;
    display: flex;
    overflow-y: scroll;
  }

  .my-button {
    width: 178px;
    height: 50px;
    color: rgba(0, 0, 0, .2);
    text-align: center;
    cursor: pointer;
    background-color: rgba(0, 0, 0, 0);
    border: 1px solid rgba(5, 5, 5, .2);
    border-radius: 15px;
    margin-left: auto;
    margin-right: auto;
    padding: 14px 29px;
    font-family: Inter;
    font-size: 18px;
    font-weight: 500;
    line-height: 1.21;
  }

  .my-button:hover, .my-button:focus {
    color: #f8f8f8;
    background-color: #9bb537;
  }

  .cont {
    flex-direction: column;
    gap: 10px;
    display: flex;
  }

  .category-option {
    text-align: left;
    cursor: pointer;
    color: rgba(5, 5, 5, .3);
    background-color: rgba(0, 0, 0, 0);
    border: none;
    padding: 10px;
    font-family: Inter;
    font-size: 14px;
    font-weight: 500;
    line-height: 1.28;
  }

  #catCont {
    width: 500px;
    height: 500px;
    background-color: #00f;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 734px;
  }
}

