.is-hidden {
  display: none;
}

.recepie-card {
  width: 250px;
}

img {
  width: 100%;
  display: block;
}

.is-favorite {
  background-color: red;
}

.favorites-list {
  background-color: #7fffd4;
}









@media screen and (min-width: 375px) {
  .popular-recipes-container {
    width: 332px;
    margin-bottom: 40px;
  }

  .popular-recipes-title {
    text-transform: uppercase;
    color: var(--main-color);
    margin-bottom: 20px;
    font-family: Inter;
    font-size: 18px;
    font-style: normal;
    font-weight: 600;
    line-height: 24px;
  }

  .popular-recipes-list {
    align-items: flex-start;
    gap: 16px;
    margin-bottom: 24px;
    display: flex;
  }

  .popular-recipes-list:nth-child(n+3) {
    display: none;
  }

  .popular-recipes-content {
    flex-direction: column;
    align-items: flex-start;
    gap: 8px;
    display: flex;
  }

  .popular-recipe-image {
    width: 64px;
    height: 64px;
    border-radius: 6px;
  }

  .popular-recipe-item-title {
    width: 136px;
    height: 18px;
    text-transform: uppercase;
    color: var(--main-color);
    font-family: Inter;
    font-size: 14px;
    font-style: normal;
    font-weight: 600;
    line-height: 18px;
  }

  .popular-recipe-description {
    color: rgba(5, 5, 5, .8);
    width: 252px;
    -webkit-line-clamp: 2;
    text-overflow: ellipsis;
    -webkit-box-orient: vertical;
    font-family: Inter;
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: 16px;
    display: -webkit-box;
    overflow: hidden;
  }
}

@media screen and (min-width: 768px) {
  .popular-recipes-container {
    width: 176px;
  }

  .popular-recipes-list:nth-child(n+3) {
    display: flex;
  }

  .popular-recipes-list {
    gap: 8px;
    margin-bottom: 16px;
  }

  .popular-recipe-image {
    width: 48px;
    height: 48px;
    border-radius: 6px;
    flex-shrink: 0;
  }

  .popular-recipes-content {
    width: 120px;
  }

  .popular-recipe-item-title {
    width: 120px;
    text-overflow: ellipsis;
    white-space: normal;
    overflow: hidden;
  }

  .popular-recipe-description {
    width: 120px;
    -webkit-line-clamp: 3;
    font-size: 10px;
    line-height: 14px;
  }
}

@media screen and (min-width: 1280px) {
  .popular-recipes-container {
    width: 235px;
  }

  .popular-recipes-list {
    gap: 16px;
    margin-bottom: 32px;
  }

  .popular-recipes-content, .popular-recipe-item-title, .popular-recipe-description {
    width: 155px;
  }
}

.recepy-container {
  flex-wrap: wrap;
  gap: 20px;
  display: flex;
}

.recepy-card-container {
  max-width: 250px;
  height: 287px;
  border-radius: 30px;
  position: relative;
}

.recepy-card-bg {
  max-width: inherit;
  height: inherit;
  border-radius: inherit;
  z-index: 1;
  object-fit: cover;
}

.recepy-card-title {
  text-transform: uppercase;
  font-family: Inter;
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  line-height: 18px;
}

.recepy-card-text {
  color: var(--secondary-color);
  max-width: inherit;
  padding-left: 16px;
  padding-right: 16px;
  position: absolute;
  bottom: 0;
}

.recepy-card-description {
  height: 32px;
  max-width: inherit;
  text-overflow: ellipsis;
  white-space: nowrap;
  -webkit-line-clamp: 2;
  white-space: pre-wrap;
  -webkit-box-orient: vertical;
  font-family: Inter;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 16px;
  display: -webkit-box;
  overflow: hidden;
}





:root {
  --main-color: #050505;
  --main-rgb-color: #050505;
  --secondary-color: #f8f8f8;
