  --active-color: #9bb537;
  --plain-white: #fff --accent-color: #eea10c;
  --additional-color: #cecdcd;
  --rare-color: rgba(160, 158, 160, .6);
  --box-shadow: 7px 5px 15px 0px var(--rare-color);
  --transition-duration: 250ms;
  --transition-time-function: cubic-bezier(.4, 0, .2, 1);
